body{
    background-color: beige;
}
h1 {
    text-align: center;
    color: #0d47a1;
    font-family:'Georgia';
    padding: 10px;
    
}
div p{
    text-align: center;
}

.container {
    display: flex;
    gap: 80px;
    align-items: center;
    justify-content: center; 
    padding: 20px;
}
.card {
    width: 200px;
    height: 300px;
    background-color: #fff;
    border-radius: 10px;
    transition: 300ms ease-in-out;
    box-shadow: 0 5px 10px 0 #0a0a0a7c;
    text-align: center;
    padding: 10px;
    /* background: url(/src/Assets/card2.jpg); */
    color: #000000;
}
.card + .card {
    margin-left: -10%;
}
.card:hover{
    transform: rotate(0deg); 
}
.card:hover ~ .card{
    z-index: 1;
    translate: 60px;
}
